.dashboard__view {
    /* background-color: green; */
    padding: 30px;
    height: 100vh;
}

.dashboard {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    /* background-color: #850606; */
}

.dashboard__element {
    display: flex;
    height: 300px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    /* background-color: #f2f2f2; */
    box-shadow: 0 4px 8px 0 rgb(0 0 0 / 20%),
        0 6px 20px 0 rgb(0 0 0 / 19%);
    overflow: auto;
}

.dashboard header {
    font-size: medium;
    font-weight: 600;
    padding: 5px 0;
    border-radius: 5px 5px 0 0;
    color: #9e9e9e;
    background-color: #30353f;
    width: 100%;
}

.dashboard__content {
    flex: 1;
    width: 100%;
}